/* global clearInterval, console, CustomFunctions, setInterval */

/**
 * Adds two numbers.
 * @customfunction
 * @param first First number
 * @param second Second number
 * @returns The sum of the two numbers.
 */
export function add(first: number, second: number): number {
  return first + second;
}

/**
 * Displays the current time once a second.
 * @customfunction
 * @param invocation Custom function handler
 */
export function clock(invocation: CustomFunctions.StreamingInvocation<string>): void {
  const timer = setInterval(() => {
    const time = currentTime();
    invocation.setResult(time);
  }, 1000);

  invocation.onCanceled = () => {
    clearInterval(timer);
  };
}

/**
 * Returns the current time.
 * @returns String with the current time formatted for the current locale.
 */
export function currentTime(): string {
  return new Date().toLocaleTimeString();
}

/**
 * Increments a value once a second.
 * @customfunction
 * @param incrementBy Amount to increment
 * @param invocation Custom function handler
 */
export function increment(
  incrementBy: number,
  invocation: CustomFunctions.StreamingInvocation<number>
): void {
  let result = 0;
  const timer = setInterval(() => {
    result += incrementBy;
    invocation.setResult(result);
  }, 1000);

  invocation.onCanceled = () => {
    clearInterval(timer);
  };
}

/**
 * Writes a message to console.log().
 * @customfunction LOG
 * @param message String to write.
 * @returns String to write.
 */
export function logMessage(message: string): string {
  console.log(message);

  return message;
}

/**
 * Возвращает ряд факториалов от 0 до n.
 * @customfunction FACTORIALROW
 * @param {number} n Число, до которого считать факториалы
 * @returns {Promise<number[][]>} Ряд факториалов от 0 до n (в виде вертикального массива)
 */
export async function factorialRow(n: number): Promise<number[][]> {
  console.log("Входное значение:", n);

  if (n < 0 || !Number.isInteger(n)) {
    console.error("Ошибка: введено отрицательное или нецелое число");
    throw new Error("Input must be a non-negative integer.");
  }

  // const mode: string | null = await OfficeRuntime.storage.getItem('resultMode');
  const mode: string | null = await Office.context.roamingSettings.get('orientationMode');
  console.log("Режим из storage:", mode);

  const resultArray: number[] = [];
  let factorial = 1;

  for (let i = 0; i <= n; i++) {
    factorial *= i || 1;
    resultArray.push(factorial);
    console.log(`Факториал ${i} = ${factorial}`);
  }

  let result: number[][];

  if (mode === 'row') {
    result = [resultArray]; // строка: одна строка, много колонок
  } else {
    // колонка: много строк, одна колонка
    result = resultArray.map(val => [val]);
  }

  console.log("Результат:", result);
  return result;
}


CustomFunctions.associate("ADD", add);
CustomFunctions.associate("CLOCK", clock);
CustomFunctions.associate("INCREMENT", increment);
CustomFunctions.associate("LOG", logMessage);
CustomFunctions.associate("FACTORIALROW", factorialRow);